body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

#root {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

/* Overide bootstrap navbar */

.navbar {
    border-radius: 0;
    border-bottom: 1px solid #a59c9c;
    margin-bottom: 0; 
}

.navbar > .container {
    width: 85%;
    max-width: 105rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

.navbar-inverse .navbar-brand {
    font-family: 'Roboto', sans-serif;
    color: #00e9de;
}

#Users-Nav-Dropdown {
    color: white;
}

.user-icon { 
    padding: 0.4rem;
    border: 1.5px solid;
    border-radius: 1.5rem;
}

.username-list-item {
    padding-left: 1rem;
}
.full-page-spinner-inactive {
    display: none;
}

.full-page-spinner-container {
    position: fixed;
    z-index: 10;
    height: 100%;
    width: 100%;
    background:rgb(0, 0, 0, 0.9);
}

.full-page-spinner {
    position: absolute;
    border: 6px solid #f3f3f3;
    border-radius: 50%;
    border-top: 6px solid #00e9de;
    width: 8rem;
    height: 8rem;
    -webkit-animation: spin 1.5s linear infinite; /* Safari */
    animation: spin 1.5s linear infinite;
    z-index: 15;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 50%;
}

.full-page-spinner-label {
    position: absolute;
    z-index: 15;
    margin: 0 auto;
    left: 0; 
    right: 0;
    color: white;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    top: 50%;
    margin-top: 9rem; 
    font-size: 1.8rem;
    color: #00e9de;
}

@media only screen and (max-width: 800px) {
    .full-page-spinner {
        width: 5rem;
        height: 5rem;
        border: 4px solid #f3f3f3;
        border-top: 4px solid #00e9de;
    }
    
    .full-page-spinner-label {
        font-size: 1.3rem;
        margin-top: 6rem; 
    }
}

/* Safari */
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.upper-section-container {
    padding: 1.5rem;
    background: #00e9de;
}
  
.current-playlist-section {
    text-align: center;
}
  
.lower-section-container {
    flex: 1 0 auto;
    background: #222;
    padding-top: 1.5rem;
}

@media only screen and (min-width: 800px) {
    .player-view-container {
      display: inline-flex;
    }
    .current-playlist-section {
      padding-right: 1rem;
      text-align: left;
    }
  }
  
@media only screen and (max-width: 800px) {
    .current-playlist-section {
        text-align: left;
    }
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.application-header {
    background-color: #222;
    padding: 0.8rem;
    padding-top: 0.8rem;
    color: white;
    font-family: 'Roboto', sans-serif;
}

.welcome-heading {
    margin-top: 1.5rem;
    font-size: 1.5rem;
}

.username-field {
    color: #00e9de
}

@media only screen and (max-width: 800px) {
    .welcome-heading {  
        font-size: 1.2rem;
    }
}
.no-video-backdrop {
    position: absolute;
    width: 42rem;
    height: 27rem;
    background: rgba(255,255,255,0.85);
    z-index: 1;
}

.no-video-backdrop-title {
    margin: 0;
    font-size: 1.8em;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color: #867f7f;
}

@media only screen and (max-width: 800px) {
    .no-video-backdrop {
        display: none;
    }
}
.youtube-player-wrapper {
    margin-bottom: 1.5rem;
    margin-left: 1.5rem;
    padding: 1.5rem;
    padding-bottom: 4rem;
    background: rgb(0, 0, 0, 0.7);
}

.youtube-player {
    pointer-events: none;
    padding-bottom: 0.5rem;
}

.youtube-player-debug {
    padding-bottom: 0.5rem;
}

.video-banner-title {
    font-size: 1.4rem;
    color: white;
    margin-top: 0;
    margin-bottom: 0;
    font-family: 'Roboto', sans-serif;
}

@media only screen and (max-width: 800px) {
    .youtube-player {
        height: 0;
        width: 0;
    }
    
    .youtube-player-debug {
        height: 0;
        width: 0;
    }

    .youtube-player-wrapper {
        padding: 1.5rem;
        margin-left: 0; 
        background: rgb(0, 0, 0, 0.7);
    }

    .video-banner-title {
        font-size: 1.3rem;
        margin-top: -2.2rem;
    }
}

.playlist-section {
    padding: 1rem;
    background: #222222;
    font-family: 'Roboto', sans-serif;
}

.playlist-header {
    color: white;
    text-align: center;
    margin-top: 0.5rem;
}

.video-playlist {
    list-style: none;
    padding: 0;
    padding-bottom: 0;
    height: 26.5rem;
    overflow: auto;
    width: 50rem;;
}

.empty-video-background {
    text-align: center;
    padding-top: 8rem;
    color: white;
    width: inherit;
    height: inherit;
}

/* Prettier scrollbar, courtesy of: https://codepen.io/devstreak/pen/dMYgeO */
.video-playlist::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.video-playlist::-webkit-scrollbar {
	width: 6px;
	background-color: #F5F5F5;
}

.video-playlist::-webkit-scrollbar-thumb {
	background-color: #000000;
}
/* End pretty scrollbar */

.music-icon {
    margin-right: 0.8rem; 
}

.playlist-item:first-child {
    color: #00e9de;
    background: rgb(0, 0, 0);
    font-weight: 500;
    border-left: 3px solid #00e9de;
}

.playlist-item {
    color: white;
    border-left: 3px solid white;
    padding: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.playlist-item:hover {
    background: rgb(0, 0, 0, 0.7);
}

.seperator {
    margin: 0;
    background: #a59c9c;
}

.no-video-text {
    margin: 0;
    font-size: 1.6rem;
}

.pause-icon {
    font-size: 5rem;
    padding-bottom: 1.5rem;
}


@media only screen and (max-width: 800px) {
    .video-playlist {
        width: inherit;
        height: 21rem;
    }

    .playlist-header {
        font-size: 1.5rem;
        margin-top: 0;
    }

    .no-video-text {
        font-size: 1.4rem;
    }

    .pause-icon {
        font-size: 3.5rem;
        padding-bottom: 1.5rem;
    }
    
    .playlist-item {
        font-size: 1.2rem;
    }
}
.main-action-tabs {
    margin: auto;
    max-width: 100rem;
    margin-bottom: 8rem;
}

/* Tabs styling */
#action-tabs > .nav-tabs > li {
    margin-bottom: 0;
}
  
#action-tabs > .nav-tabs > li > a {
    border-radius: 0;
    background: #222;
    color: white;
    border-top: 2.5px solid transparent;
    margin-bottom: 0; 
}
  
#action-tabs > .nav-tabs > li > a:hover {
    border: 0;
    color: #00e9de;
    border-top: 2.5px solid transparent;
}
  
#action-tabs > .nav-tabs > li.active > a {
    border-radius: 0;
    color: #00e9de;
    border-left: 0;
    border-right: 0; 
    border-top: 2.5px solid #00e9de;
}
.suggestion-list-item {
    list-style: none;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.suggestion-thumbnail {
    height: 8rem;
    width: 11.5rem;
}

.video-suggestion-seperator {
    margin-top: 1.1rem;
    margin-bottom: 1.1rem; 
}

.add-video-button {
    margin-top: 0rem;
    border-radius: 0;
    background: #222;
    border: 0;
    margin-right: 1rem; 
}

.add-video-button:hover, .add-video-button:focus {
    background: #222;
    border: 0;
    border: white;
    color: #00e9de;
}

/* Icons */
.plus-icon {
    font-size: 2.5rem;
}

@media only screen and (max-width: 800px) {
    .suggestion-thumbnail {
        height: 5rem;
        width: 6.5rem;
    }

    .add-video-button {
        border: 0;
        margin-right: 0; 
    }
}
.suggestion-description {
    color: white;
    font-family: 'Roboto', sans-serif;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    display: flex;
    flex-direction: column; 
    text-align: left;
    width: 100%;
