    overflow: hidden;
    text-overflow: ellipsis; 
}

.suggestion-title {
    font-size: 1.5rem;
    margin: 0;
}

.video-duration, .video-channel {
    font-size: 1.3rem;
    margin-top: 1.2rem;
}

@media only screen and (max-width: 800px) {
    .suggestion-description {
        padding-left: 0.8rem;
        padding-right: 0;
    }

    .suggestion-title {
        font-size: 1.2rem;
    }

    .video-duration, .video-channel {
        font-size: 1.1rem;
        margin-top: 0.4rem;
    }

}
.video-suggestion-list {
    padding: 0;
    padding-right: 1rem;
    height: 60rem;
    overflow: auto;
}

.video-suggestion-list::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.video-suggestion-list::-webkit-scrollbar {
	width: 6px;
	background-color: #F5F5F5;
}

.video-suggestion-list::-webkit-scrollbar-thumb {
	background-color: #000000;
}

@media only screen and (max-width: 800px) {
    .video-suggestion-list {
        height: 50rem;
    }
}
