.lobby-footer-wrapper {
    position: fixed;
    width: 100%;
    bottom: 8rem;
    font-family: 'Roboto', sans-serif;
}

.lobby-footer {
    position: absolute;
    width: 100%;
    color: white;
    height: 8rem;
    background: #2d2d2d;
