}
.progress {
    height: 0.4rem;
    margin-bottom: 0;
    overflow: hidden;
    background-color: #f5f5f5;
    border-radius: 0;
}

.progress-bar {
    background-color: #00e9de;
}
.footer-progress-duration {
    font-size: 1.2rem;
    display: block;
    margin-bottom: 0.3rem;
}

.footer-end-time {
    float: right;
}
.footer-controls {
    width: 73rem;
    padding-left: 1rem;
    margin: auto;
    text-align: center;
    margin-top: 0;
}

.footer-control-buttons {
    text-align: center;
    margin-bottom: 0.55rem;
}

.footer-progress-bar {
    text-align: left;
    display: block;
}

/* Buttons and volume slider */
.footer-control-buttons {
    display: inline-flex;
}

.skip-video-button, .favorite-video-button {
    margin-right: 1.5rem;
    padding: 0.3rem 0.5rem;
    background: none;
    border: solid #00e9de 1px;
    color: #00e9de;
}

.skip-video-button {
    padding: 0.3rem 0.5rem;
}

.favorite-video-button {
    border-radius: 1.5rem;
}

.skip-video-button:hover,
.skip-video-button:focus,
.favorite-video-button:focus,
.favorite-video-button:hover {
    background: none;
    border: solid #00e9de 1px;
    color: white;
}

.skip-icon {
    margin-left: 0.2rem;
}
.footer-volume-controls {
    display: inline-flex;
    margin-left: 1.5rem;
    height: 2.5rem;
    margin-top: 1.3rem; 
}

/* Slider Override */
.input-range {
    width: 9rem;
    margin-left: 0.9rem;
    margin-top: 0.8rem;
}

.input-range__slider {
    background: #00e9de;
    border: 3px solid #00e9de;
}

.input-range__track--active {
    background: #00e9de;
}

.input-range__label-container {
    display: none;
}
/* End slider override */

.mute-button {
    background: none;
    border: solid #00e9de 1px;
    color: #00e9de;
    padding: 0.3rem 0.5rem;
}

.mute-button:hover,
.mute-button:focus {
    background: none;
    border: solid #00e9de 1px;
    color: white;
}

.mute-button {
    border-radius: 1.5rem;
    font-size: 0.8em;
}

@media only screen and (max-width: 800px) {
    .skip-video-button, .mute-button {
        font-size: 1rem;
    }
}
.lobby-controls {
    padding: 0.7rem;
    margin: auto;
    width: 15rem;
    background: rgb(0, 0, 0, 0.7);
    border-radius: 0.8rem; 
}

.thumbs-up-section, .thumbs-down-section {
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.thumbs-up-section {
    margin-right: 0.8rem
}

.thumbs-down-section {
    margin-left: 0.8rem;
}

.thumbs-up-button, .thumbs-down-button {
    background: #222222;
    border: none;
    border-radius: 1.6rem;
    color: #00e9de;
}

.thumbs-up-button:hover,
.thumbs-down-button:hover,
.thumbs-up-button:focus,
.thumbs-down-button:focus
{
    background: #222222;
    color: white;
}
